.ResourceStack-container {
  position: relative;
}

.ResourceStack-circle {
  border-width: 0.3vh;
  border-radius: 50%;
  border-color: rgba(0, 0, 0, 0.5);
  border-style: solid;
  -webkit-box-shadow: black 0.5vh 0.5vh 0.5vh 0;
          box-shadow: black 0.5vh 0.5vh 0.5vh 0;
  width: 7vh;
  height: 7vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.ResourceStack-circle-base {
  opacity: 0.5;
  border-style: dotted;

  background-size: 4vh 4vh;
  background-repeat: no-repeat;
  background-position: center center;
}

.ResourceStack-circle-highlighted {
  border-color: black;
  font-weight: bold;
}

.ResourceStack-circle-head {
  position: relative;
}

.ResourceStack-circle-rest {
  position: absolute;
}

.ResourceStack-circleText {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 3.5vh;
}



