.ResourceBox-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 2.5vh;
  height: 2.5vh;
  font-size: 2vh;
}

.ResourceBox-container + .ResourceBox-container {
  margin-top: 0.75vh;
}

.ResourceBox-innerShadow {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.ResourceBox-outerShadow,
.ResourceBox-innerShadow {
  -webkit-transition: opacity 0.3s ease-out,
              -webkit-box-shadow 0.3s ease-out;
  transition: opacity 0.3s ease-out,
              -webkit-box-shadow 0.3s ease-out;
  -o-transition: box-shadow 0.3s ease-out,
              opacity 0.3s ease-out;
  transition: box-shadow 0.3s ease-out,
              opacity 0.3s ease-out;
  transition: box-shadow 0.3s ease-out,
              opacity 0.3s ease-out,
              -webkit-box-shadow 0.3s ease-out;
  width: 100%;
  height: 100%;
}

.ResourceBox-available {
  color: whitesmoke;
}

.ResourceBox-unavailable {
  color: white;
}

.ResourceBox-available .ResourceBox-outerShadow {
  -webkit-box-shadow: whitesmoke 0 0 0.5vh 0.5vh;
          box-shadow: whitesmoke 0 0 0.5vh 0.5vh;
}

.ResourceBox-unavailable .ResourceBox-outerShadow {
  -webkit-box-shadow: whitesmoke 0 0 0 0;
          box-shadow: whitesmoke 0 0 0 0;
}

.ResourceBox-available .ResourceBox-innerShadow {
  opacity: 1.0;
  -webkit-box-shadow: inset black 0 0 0 0;
          box-shadow: inset black 0 0 0 0;
}

.ResourceBox-unavailable .ResourceBox-innerShadow {
  opacity: 0.65;
  -webkit-box-shadow: inset black 0 0 1.5vh 0;
          box-shadow: inset black 0 0 1.5vh 0;
}




