.Factory-container {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: end;
      align-items: flex-end;

  width: 14.8vw;
  height: 18vh;
  padding: 0.5vw;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: white;
  background-size: 5vh 5vh;
  background-repeat: no-repeat;
  background-position: 0.5vh 0.5vh;
  border: 0.3vh solid;
  border-radius: 1vh;
  margin: 0 0.3vh;

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;
}

.Factory-overlay {
  position: absolute;
}

.Factory-costContainer {
  top: 0.5vh;
  right: 0.5vh;

  z-index: 2;
}

.Factory-pointsContainer {
  bottom: 0.5vh;
  left: 1vh;

  font-size: 5vh;

  z-index: 2;
}



